.header-color{
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   }
.Landing-grid{
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  /*position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;*/
}
.avatar-img{
  height:300px;
  padding-top: 5pm;
  position: relative;
}
.banner-text{
  background-color: #000000;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
  text-align: center;
}
.banner-text hr{
  border-top: 5px dotted white;
  margin: auto;
  width: 50%;
}
.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
  text-align: center;
}
.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;
}
.projects-grid{
  display: flex;
  justify-content: space-between;
}
/*-------CONTACT PAGE-------*/
.contact-body{
  margin: auto;
  background-color: gray;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  
}
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: white;
  color: #000000;
}
.contact-grid h2{
  font-family: 'Anton',sans-serif;
}
.contact-grid p{
  font-family: 'Oxygen',sans-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black ;
  width: 50%;
  margin: auto;
}
.contact-list i{
  padding-right: 5em;
}
/*---------RESUME PAGE----------*/
.resume-right-col{
  background: #27221F;
  color: white;
}